<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>

    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>

    <el-form-item prop="vertifyCode">
      <el-input type="text" v-model="ruleForm2.vertifyCode" auto-complete="off" placeholder="验证码"></el-input>
    </el-form-item>
    <el-form-item prop="vertifyCodePic">
      <el-image :src="ruleForm2.vertifyCodePic"></el-image>
    <el-link type="primary" @click="refreshVertifyCode">{{ruleForm2.vertifyText}}</el-link>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-link href="/resetpassword" target="_blank">忘记密码</el-link>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
    <el-form-item style="width:100%;">
      <el-link href="/rigester" target="_blank">注册</el-link>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>
  import { requestLogin,getVertifyCode,postVertifyCode } from '../api/api';
  import crypto from 'crypto';
  import sleep from '@/utils/utils'
  //import NProgress from 'nprogress'
  export default {
    data() {
      
      return {
        
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: '',
          vertifyCode:"",
          vertifyCodePic:"/api/identifycode/pic",
          vertifyText:"看不清,换一张",
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ],
          vertifyCode:[
            { required: true, message: '请输入验证码', trigger: 'blur'},
          ]
        },
        checked: true
      };
    },
    methods: {
      // 刷新验证码
      refreshVertifyCode(){
        this.ruleForm2.vertifyCodePic="/api/identifycode/pic?name="+String(Math.random());
      },
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      gotomainpage(){
        this.$router.push({path:"/"});
      },
      handleSubmit2(ev) {
        var _this = this;
        // this.vertifyCodeCheck();
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            this.logining = true;
            //NProgress.start();
            var md5 = crypto.createHash("md5")
            md5.update(this.ruleForm2.checkPass)//this.pw2这是你要加密的密码
            var pwd = md5.digest('hex')//this.pw这就是你加密完的密码，这个往后台传就行了
            var loginParams = { username: this.ruleForm2.account, password: pwd ,vertifycode:this.ruleForm2.vertifyCode};
            // console.log(loginParams);
            requestLogin(loginParams).then(data => {
              this.logining = false;
              //NProgress.done();
              let { msg, code,user } = data;
              if (code !== 200) {
                this.$message({
                  message: msg,
                  type: 'error'
                });
                this.refreshVertifyCode();
              } else {
                sessionStorage.setItem('user', JSON.stringify(user));
                this.$message({
                  message: msg,
                  type: 'success'
                });
                this.$store.commit("updata_database_name");
                this.$store.commit("update_table_title","tabletitle");
                setTimeout(this.gotomainpage,2000);
              }
            });
            
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


